html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

a:link {
  color: white;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  text-decoration: none; }

a.pkpLink:link {
  color: #888888;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  text-decoration: none; }

a:visited {
  color: white;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  text-decoration: none; }

a.pkpLink:visited {
  color: #888888;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  text-decoration: none; }

a:hover {
  color: #888888; }

body {
  background-color: white; }

.dojazd {
  font-family: 'Poppins', sans-serif;
  font-style: normal; }
  .dojazd__header {
    font-size: 32px; }
  .dojazd__mapContainer {
    margin-top: 32px; }
  .dojazd__boldContent {
    font-weight: bold; }

.footer {
  background-color: black;
  height: 60px;
  display: flex;
  flex-direction: row;
  align-items: center; }
  .footer__text {
    color: white;
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    margin-right: 32px;
    margin-left: 32px;
    font-size: 24px; }
  .footer__instaIconContainer {
    margin-top: 3px;
    margin-left: 16px; }

iframe {
  height: 450px;
  width: 1000px;
  resize: both;
  overflow: auto; }

.kontakt {
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  margin-bottom: 48px; }
  .kontakt__description1 {
    color: red; }
  .kontakt__descriptionHeader {
    font-size: 32px;
    font-style: bold; }
  .kontakt__header {
    font-size: 32px; }
  .kontakt__flex {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly; }

.mainBody {
  max-width: 100%;
  width: 1012px;
  margin: auto; }

.navigation {
  position: fixed;
  width: 100%;
  top: 0px;
  left: 0px;
  height: 60px;
  box-sizing: border-box;
  background-color: black;
  display: flex;
  flex-direction: row;
  justify-content: flex-start; }
  .navigation__iteam {
    display: inline;
    padding: 0px;
    padding-left: 16px;
    padding-right: 16px;
    color: white;
    font-size: 24px; }
  .navigation__list {
    list-style: none;
    padding: 0;
    display: flex;
    margin-right: 48px; }
  .navigation__logo {
    margin-left: 32px;
    margin-right: 64px;
    height: 56px; }
  .navigation__iteamColumn {
    display: inline;
    padding: 0px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 5px;
    color: white;
    font-size: 24px; }

.navigationColumn {
  position: fixed;
  display: none; }

.oferta {
  font-family: 'Poppins', sans-serif;
  font-style: normal; }
  .oferta__header {
    font-size: 32px; }
  .oferta__offerContainer {
    display: block;
    max-width: 100%;
    width: 850px;
    margin: auto;
    margin-top: 64px; }

.pkpLink {
  color: #888888; }

.salon {
  margin-top: 140px;
  font-family: 'Poppins', sans-serif;
  font-style: normal; }
  .salon__header {
    font-size: 32px; }
  .salon__img {
    float: right;
    margin: 0px 16px 16px 32px;
    display: block;
    max-width: 400px;
    width: 100%; }
  .salon__photoContainer {
    padding-left: 16px; }

@media (min-width: 901px) {
  .navigation__switcher {
    display: none; } }

@media (max-width: 900px) {
  iframe {
    height: 300px;
    width: 300px;
    resize: both;
    overflow: auto; }
  .flex__iteam {
    margin-top: 32px; }
  .kontakt__flex {
    flex-direction: column;
    justify-content: space-evenly; }
  .kontakt__descriptionHeader {
    margin-top: 16px;
    margin-bottom: 16px; }
  .navigation__logo {
    margin-left: 24px;
    margin-right: 64px;
    margin-top: 14px;
    height: 32px; }
  .navigation__switcher {
    position: absolute;
    right: 16px;
    top: 16px;
    background-color: black;
    color: white;
    border: 2px solid white; }
  .navigation__list {
    display: none; }
  .navigation__listColumn {
    display: flex;
    flex-direction: column;
    padding-left: 0px; }
  .navigationColumn--visible {
    position: fixed;
    width: 100%;
    top: 60px;
    left: 0px;
    height: 180px;
    box-sizing: border-box;
    background-color: black;
    display: flex;
    flex-direction: row;
    justify-content: center; }
  .mainBody {
    padding-left: 16px;
    padding-right: 16px; } }

@media (max-width: 600px) {
  .mainBody {
    display: flex;
    flex-direction: column;
    justify-content: center; }
  .salon {
    margin-top: 80px; } }
